.nav-bar {
    background-color: #C3C3C3;
    box-shadow: inset 0.15rem 0.15rem 0 #DBDBDB;
    display: flex;
    position: fixed;
    align-items: center;
    bottom: 0;
    width: 99.3%;
    padding: 5px;
}

.nav-start-button {
    font-size: 1.1rem;
    font-weight: 800;
    width: 75px;
    padding: 7px;
    text-align: center;
    line-height: 1rem;
}

.nav-links {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0 0.75rem;
    margin: 0 0.5rem;
    border-left: 0.15rem solid #828282;
    border-right: 0.15rem solid #D8D8D8;
    box-shadow: inset 0.15rem 0 0 #D8D8D8, inset -0.15rem 0 0 #828282;
}

.nav-tab-items {
    display: flex;
    align-items: center;
}